import { isSocialWall, isTagEmbed, isTagbox } from "../../../../../actions/api";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../../Elements/PostContent/PostContent'
import CTAButton from "../../../Elements/CTA/CtaButton";
import Rating from "../../../Elements/Rating/Rating";
import ShareAndSocialActions from "./ShareAndSocialActions";
import { IS_THEME, PostTrimContent } from '../../../../../utils'

const trim_content_length = 50
const Card = ({ itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, onClickToCTA, wall }) => {
  const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) ? true : false;
  const rating = (itemData.rating > 0) ? true : false;
  const contentClass = (itemData.rating > 0) ? 'tb_g_s_rating_content' : 'tb_g_s_content'
  const tagEmbedReviewContentClass = isTagEmbed && IS_THEME(5) ? 'tb_g_s_rating_content_review' : ''
  const textDecorate = (itemData.type === 1 && personalization.textDecorate) ? 'tb_g_s_text_decoration tb_g_s_text_post' : '';
  const ctaStatus = (itemData.cta && Object.keys(itemData.cta).length > 0 && itemData.cta.status) ? true : false;
  const cardActions = (ctaStatus || ThemeRule.socialAction || ThemeRule.shareOption) && !isSocialWall ? true : false;
  const postTextContent = itemData.content//personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  const outerpadding = isSocialWall ? { padding: ThemeRule.spacing } : null
  return (
    <div id={`tb-cc-post-${itemData.id}`} className={`tb_g_s_post_wrapper`} style={{
      width: `${adjustWidth}%`, padding: personalization.padding / 2
    }} tb-network={itemData.network.icon}>
      <div className="tb_g_s_post_in" onClick={clickToShowPopUp(itemIndex, itemData)} style={outerpadding} >

        {isTagEmbed || IS_THEME(62) || IS_THEME(78) || IS_THEME(66) ? (mediaType ?
          <div className="tb_g_s_post_media_wrapp">
            <Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} />
          </div>
          : '') : null}

        <div className="tb_g_s_contant_wrapper">
          <AuthorDetails key={`img${itemIndex}_${itemData.id}`} ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
          {rating && IS_THEME(5) ? <div className="tb_g_s_rating__"><Rating rating={itemData.rating} network={itemData.network} /> </div> : ''}
          {((!ThemeRule.hideContent || itemData.type == 1) && !IS_THEME(63)) ? <PostContent item={itemData} contentClass={`${contentClass} ${tagEmbedReviewContentClass} ${textDecorate}`} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> : ''}
          {rating && !IS_THEME(5) ? <div className="tb_g_s_rating__"><Rating rating={itemData.rating} network={itemData.network} /> </div> : ''}
        </div>

        {(!isTagEmbed && !IS_THEME(62) && !IS_THEME(78) && !IS_THEME(66)) ? (mediaType ?
          <div className="tb_g_s_post_media_wrapp">
            <Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} />
          </div>
          : '') : null}

        {cardActions || ctaStatus || ThemeRule.shareOption ?
          <div className="tb_g_s_post_actions">
            {ctaStatus ? <div className="tb_g_s_post_cta"><CTAButton ctaClass={'tb_g_s_post_cta_btn'} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div> : ''}
            {cardActions ? <ShareAndSocialActions itemData={itemData} ThemeRule={ThemeRule} /> : ''}
          </div>
          : ''}
      </div>
    </div>

  );
}
export default Card